ng: 1rem;\n  background-color: #333;\n  color: white;\n  width: 100%;\n  box-sizing: border-box;\n}\n\n.navbar .brand h1 {\n  margin: 0;\n  font-size: 2rem;\n  color: white;\n}\n\n.nav-menu {\n  display: flex;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  gap: 1rem;\n  position: absolute;\n  right: 30px;\n  margin-right: 20px;\n}\n\n.nav-menu li {\n  margin: 0;\n}\n\n.nav-menu li a {\n  color: white;\n  text-decoration: none;\n  padding: 0.5rem 1rem;\n  border-radius: 5px;\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.nav-menu li a:hover,\n.nav-menu li a:focus {\n  background-color: #555;\n}\n\n.nav-content {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.menu-button {\n  display: none;\n  background: none;\n  border: none;\n  color: white;\n  font-size: 1.5rem;\n  cursor: pointer;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.drawer {\n  display: none;\n}\n\nfooter #footerTools {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  gap: 8px;\n}\n\n/* .fa fa-heart-o{\n  min-height: 18px;\n  min-width: 18px;\n} */\nfooter p {\n  margin-top: 16px;\n  text-align: center;\n  color: #aaaaaa;\n}\n\nfooter p a {\n  color: #db0000;\n  text-decoration: none;\n  min-height: 44px;\n  min-width: 44px;\n}\n\n.hero-container {\n  display: flex;\n  justify-content: center;\n  margin-top: 30px;\n  align-items: center;\n  min-height: 500px;\n  width: 100%;\n  text-align: center;\n  background-position: center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  position: relative;\n  /* z-index: 1; */\n}\n\n.hero-content {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  color: white;\n  padding: 20px 0;\n  box-sizing: border-box;\n  /* z-index: 1; */\n}\n\n.hero-title {\n  font-size: 2rem;\n  margin: 0;\n}\n\n.hero-subtitle {\n  font-size: 1.2rem;\n  margin: 10px 0 0;\n}\n\n.hero-content.hidden {\n  opacity: 0;\n  transform: translateY(-50px); /* Sesuaikan dengan kebutuhan */\n}\n\n.restaurant-detail {\n  margin: 20px;\n  background-color: white;\n  /* border: 1px solid #e0e0e0; */\n  padding: 20px;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  height: auto;\n}\n\n.restaurant__name {\n  margin: 0;\n  font-size: 24px;\n}\n\n.restaurant__poster {\n  width: 100%;\n  height: auto;\n  border-radius: 8px;\n}\n\n.restaurant__info {\n  margin-top: 20px;\n}\n\n.restaurant__info h2 {\n  margin-bottom: 10px;\n}\n\n.restaurant__info ul {\n  list-style: none;\n  padding: 0;\n}\n\n.restaurant__info li {\n  margin-bottom: 10px;\n}\n\n.restaurant__info li h3 {\n  margin: 0;\n  font-size: 18px;\n}\n\n.restaurant__info li p {\n  margin: 0;\n}\n\n.overview__restaurant {\n  margin-top: 20px;\n}\n\n.overview__restaurant h2 {\n  margin-bottom: 10px;\n}\n\n.overview__restaurant p {\n  margin: 0;\n}\n\n.restaurant__item {\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  overflow: hidden;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n  background-color: #fff;\n  margin-bottom: 20px;\n}\n\n.restaurant__item:hover {\n  transform: translateY(-5px);\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n}\n\n.restaurant-item__header {\n  position: relative;\n}\n\n.city-label {\n  position: absolute;\n  top: 10px;\n  left: 10px;\n  background-color: #db0000;\n  color: #fff;\n  padding: 4px 8px;\n  border-radius: 4px;\n}\n\n.city-label-text {\n  font-size: 14px;\n}\n\n.rating {\n  display: flex;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.rating i {\n  color: #db0000;\n  margin-right: 5px;\n}\n\n.card-title a {\n  color: #333;\n  text-decoration: none;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.card-title a:hover {\n  text-decoration: underline;\n}\n\n.card-text {\n  margin-bottom: 10px;\n}\n\n.review {\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  padding: 10px;\n  margin-bottom: 20px;\n}\n\n.review .name {\n  font-weight: bold;\n}\n\n.review .date {\n  color: #666;\n  font-size: 14px;\n}\n\n.favorite__button {\n    /* width: 55px;\n    height: 55px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #db0000;\n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    border-radius: 50%;\n    border: 0;\n    font-size: 18px;\n    color: white;\n    cursor: pointer;\n     */\n    font-size: 18px;\n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    background-color: #db0000;\n    color: white;\n    border: 0;\n    border-radius: 50%;\n    width: 55px;\n    height: 55px;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    min-height: 44px;\n    min-width: 44px;\n    display: inline-block;\n}\n\n.review-form-container {\n  margin-top: 20px;\n  padding: 20px;\n  background-color: white;\n  border: 1px solid #ccc;\n  border-radius: 8px; \n}\n\n.review-header {\n  font-size: 1.2rem;\n  margin-bottom: 10px;\n}\n\n#review-form label {\n  display: block;\n  margin-bottom: 5px;\n}\n\n#review-form input[type=\"text\"],\n#review-form textarea {\n  width: 100%;\n  padding: 8px;\n  margin-bottom: 10px;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  box-sizing: border-box; /* Tambahkan untuk menghitung padding dan border dalam width */\n}\n\n#review-form button[type=\"submit\"] {\n  background-color: #007bff; /* Warna latar belakang biru */\n  color: white; /* Warna teks putih */\n  padding: 10px 20px; /* Padding 10px atas dan bawah, 20px kiri dan kanan */\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  font-size: 1rem;\n}\n\n#review-form button[type=\"submit\"]:hover {\n  background-color: #0056b3; /* Warna latar belakang biru gelap saat hover */\n}\n\na {\n  display: inline-block;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 10px;\n  text-decoration: none; /* Hapus dekorasi teks default */\n}\n\nbutton {\n  min-width: 44px;\n  min-height: 44px;\n  padding: 10px;\n  border: none; /* Hapus border default */\n  cursor: pointer;\n}\n\n#review-name {\n  min-height: 44px;\n}\n\n#query {\n  width: 40%;\n  padding: 12px 20px;\n  margin: 8px 0;\n  box-sizing: border-box;\n  border: 2px solid #ccc;\n  border-radius: 4px;\n  font-size: 16px;\n}\n\n.hero {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 100%;\n  height: 100vh;\n  background-position: center;\n  background-color: #000;\n  position: relative;\n}\n\n.hero img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  opacity: 0.5;\n  position: absolute;\n}"],"sourceRoot":""}]);
